@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #FFF5EE;
}


.expenses {
  padding: 1rem;
  background-color: #475927;
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}
.expense-item {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  padding: 0.5rem;
  margin: 1rem 0;
  border-radius: 12px;
  background-color: #b0c983;
}

.expense-item__description {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-flex-flow: column-reverse;
          flex-flow: column-reverse;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.expense-item h2{
  color: #3a3a3a;
  font-size: 1rem;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 0 1rem;
  color: white;
}

.expense-item__price {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: #609f60;
  border: 1px solid #d6ebad;
  padding: 0.5rem;
  border-radius: 12px;
}

@media (min-width: 580px) {
  .expense-item__description {
      -webkit-flex-direction: row;
              flex-direction: row;
      -webkit-align-items: center;
              align-items: center;
      -webkit-justify-content: flex-start;
              justify-content: flex-start;
      -webkit-flex: 1 1;
              flex: 1 1;
  }

  .expense-item__description h2 {
      font-size: 1.25rem;
  }

  .expense-item__price {
      font-size: 1.25rem;
      padding: 0.5rem 1.5rem;
  }
}
.expense-date {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 5.5rem;
  height: 5.5rem;
  border: 1px solid #ececec;
  background-color: #FFEFD5;
  color: #666666;
  border-radius: 12px;
  -webkit-align-items:center;
          align-items:center;
  -webkit-justify-content: center;
          justify-content: center;
}

.expense-date__month {
  font-size: 0.75rem;
  font-weight: bold;
}

.expense-date__year {
  font-size: 0.75rem;
}

.expense-date__day {
  font-size: 1.5rem;
  font-weight: bold;
}
